<template>
  <div class="goTop">
    <button type="button" class="back-top small" @click="backTop()" v-show="backTopShow">
      <img src="../images/common/goTop.png" alt="">
    </button>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        backTopShow:false
      }
    },
    components: {},
    mounted() {
      window.addEventListener('scroll', this.handleScroll)
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods: {
      backTop() {
        let back = setInterval(() => {
          if(document.body.scrollTop){
            document.body.scrollTop-=20;
          }else {
            clearInterval(back)
          }
        });
      },
      handleScroll(){
        if (document.documentElement.scrollTop || document.body.scrollTop > window.screen.height) {
          this.backTopShow=true;
        }
        else {
          this.backTopShow=false;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../style/mixin";
  .goTop{

  }
  .back-top {
    @include fjc;
    position: fixed;
    right: .4rem;
    bottom: 1rem;
    border:0;
    z-index: 99;
    background: #fff;
    height: .68rem;
    border-radius: .34rem;
    width: .68rem;
    box-shadow: 0rem 0.05rem 0.25rem 0.02rem rgba(198,198,198,0.5);
    img{
      width: .42rem;
      height: .42rem;
    }
  }
</style>



