<template>
    <div class="moduleOne">
      <div class="moo">
        <dl>
          <dt>{{titleDt}}</dt>
          <dd>{{titleDd}}</dd>
        </dl>
      </div>
      <div class="mot">
        <span>{{titleSpan}}</span>
        <div v-if="p1" class="motimg">
          <img src="../../images/case/private/p1.jpg" alt="">
        </div>
        <div v-if="h1" class="motimg">
          <img src="../../images/case/home/h1.jpg" alt="">
        </div>
        <div v-if="b21" class="motimg">
          <img src="../../images/case/birthday/b21.jpg" alt="">
        </div>
        <div v-if="d1" class="motimg">
          <img src="../../images/case/business/d1.jpg" alt="">
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    props: ['titleDt','titleDd','titleSpan','p1','h1','b21','d1'],
    name: "",
    data() {
      return {

      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .moduleOne{
    padding-right: .7rem;
    padding-left: .7rem;
    .moo{
      display: flex;
      justify-content: flex-end;
      dl{
        dt{
          @include sc(.26rem,#000);
          padding-bottom: .15rem;
          text-align: right;
        }
        dd{
          width: 4.9rem;
          @include sc(.24rem,#000);
          text-align: right;
        }
      }
    }
    .mot{
      @include fjsb;
      margin-top: .9rem;
      span{
        @include sc(.26rem,#000);
        -webkit-writing-mode: lr-tb;
        -ms-writing-mode: lr-tb;
        writing-mode: lr-tb;
        width: .24rem;
      }
      .motimg{
        @include wh(4.2rem,2.4rem);
        background: red;
        img{
          @include wh(100%,100%);
        }
      }
    }

  }

</style>



