<template>
  <div class="footer">
    <transition name="slide-fade">
      <div @click = "onceyy('/createactive')" class="onceyy" v-show="isShow">{{$t('home.reservationNow')}}</div>
    </transition>
    <span @click="showSide"><img src="../../images/common/yy.png" alt=""></span>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        isShow: false
      }
    },
    methods:{
      showSide(){
        this.isShow = !this.isShow
      },
      onceyy(path){
        this.$router.push(path)
        this.isShow=false;
      }
    },
    components:{
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .footer{
    @include fjc;
    position: fixed;
    right: .4rem;
    bottom: 1.95rem;
    z-index: 99;
    span{
      position: relative;
      @include sc(.3rem, #000);
      @include fjc;
      background: #fff;
      height: .68rem;
      border-radius: .34rem;
      width: .68rem;
      box-shadow: 0 0.05rem 0.25rem 0.02rem rgba(198,198,198,0.5);
      img{
        width: .48rem;
        height: .48rem;
      }
    }
    .onceyy{
      @include sc(.24rem, #000);
      @include wh(2rem, .68rem);
      background: white;
      line-height: .7rem;
      padding-left: .2rem;
      border-bottom-left-radius: .35rem;
      border-top-left-radius: .35rem;
      margin-right: -.3rem;
      box-shadow: 0 0.05rem 0.25rem 0.02rem rgba(198,198,198,0.5);
    }

    .slide-fade-enter-active {
      transition: all .2s ease;
    }
    .slide-fade-leave-active {
      transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to {
      transform: translateX(.1rem);
      opacity: 0;
    }

    /*.slide-fade-enter-to, .slide-fade-leave-to{
      transition: transform 0.2s;
      transform:translate(0px,0px);
    }
    .slide-fade-enter, .slide-fade-leave-to{
      opacity: 0;
      -webkit-transform:translate(.5rem,0px);
      transform:translate(.5rem,0px);
      -webkit-transition:opacity 0.2s ease-in-out 0.2s,-webkit-transform 0.2s ease-in-out;
      transition: opacity 0s ease-in-out 0.2s,transform 0.2s ease-in-out;
    }*/
  }
</style>



