<template>
    <div class="moduleTwo">
      <p>{{twoP}}</p>
      <div class="mto">
        <span>{{twoSpan}}</span>
        <div v-if="p2" class="mtoimg">
          <img src="../../images/case/private/p2.jpg" alt="">
        </div>
        <div v-if="h2" class="mtoimg">
          <img src="../../images/case/home/h2.jpg" alt="">
        </div>
        <div v-if="b22" class="mtoimg">
          <img src="../../images/case/birthday/b23.jpg" alt="">
        </div>
        <div v-if="d21" class="mtoimg">
          <img src="../../images/case/business/d21.jpg" alt="">
        </div>
      </div>
      <div class="mto2">
        <span>{{twoSpan2}}</span>
        <div v-if="p3" class="mto2img">
          <img src="../../images/case/private/p3.jpg" alt="">
        </div>
        <div v-if="h3" class="mto2img">
          <img src="../../images/case/home/h3.jpg" alt="">
        </div>
        <div v-if="b23" class="mto2img">
          <img src="../../images/case/birthday/b22.jpg" alt="">
        </div>
        <div v-if="d22" class="mto2img">
          <img src="../../images/case/business/d22.jpg" alt="">
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    props: ['twoP','twoSpan','twoSpan2','p2','p3','h2','h3','b22','b23','d21','d22'],
    name: "",
    data() {
      return {

      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .moduleTwo{
    padding-left: .45rem;
    padding-right: .2rem;
    p{
      @include sc(.26rem,#000);
      text-align: justify;
      text-indent: .45rem;
      line-height: .36rem;
      padding-right: .5rem;
    }
    .mto{
      display: flex;
      justify-content: flex-end;
      padding-top: .8rem;
      span{
        @include sc(.26rem,#000);
        padding-right: .5rem;
      }
      .mtoimg{
        width: 4rem;
        height: 2.6rem;
        background: red;
        img{
          @include wh(100%,100%)
        }
      }
    }
    .mto2{
      display: flex;
      justify-content: flex-start;
      width: 3.58rem;
      height: 3.58rem;
      position: relative;
      margin-top: .8rem;
      overflow: hidden;
      span{
        position: absolute;
        right: .18rem;
        top: .4rem;
        z-index: 9;
        @include sc(.26rem,#fff);
      }
      .mto2img{
        position: absolute;
        @include wh(3.58rem,3.58rem);
        background: red;
        img{
          @include wh(100%,100%)
        }
      }
    }

  }
</style>



